// верхний бургер



.cat-menu-box {


	&.show {
		.cat-menu {
			opacity: 1;
			visibility: visible;
		}
		.burger {
			span, &::before, &::after {
				transition: transform 1s;
			}

			span {
				transform: translateY(0) rotate(-45deg);
			}

			&::after {
				display: none;
			}

			&::before {
				top: 50%;
				transform: rotate(45deg);
			}


		}
	}

	.burger {
		width: 16px;
		height: 14px;
		position: relative;
		cursor: pointer;

		span, &::before, &::after {
			position: absolute;
			left: 0;
			display: inline-block;
			min-width: 100%;
			height: 2px;
			background-color: #000;
			content: '';
		}

		span {
			top: 50%;
			transform: translateY(-50%);

		}
		&::before {
			top: 0;
		}
		&::after {
			bottom: 0;
		}




	}

	.cat-menu {
		opacity: 0;
		visibility: hidden;
		height: 0;


		&__nav {
			background-color: #fff;
			position: absolute;
			bottom: 0;
			left: 15px;
			transform: translateY(100%);
			z-index: 100;
			width: 100%;
			padding-bottom: 100px;


			li {

				font-size: 18px;
				font-weight: 400;
				border-radius: 5px;
				margin-bottom: 5px;
			}


			// 1 уровень
			& > ul {
				.grid-submenu {
					top: 0;
					position: absolute;
					left: 360px;
					display: grid;
					grid-template-columns: 1fr 1fr 1fr;
					width: 100%;
					max-width: 920px;
					color: #000;
					& > ul:nth-child(2), & > ul:nth-child(3) {
						margin-top: 60px;
						.submenu {
							cursor: pointer;


						}
					}
				}

				& > li {
					display: flex;
					justify-content: space-between;
					min-height: 43px;
					font-size: 18px;
					padding: 5px 15px 5px 12px;
					width: 312px;
					a {
						display: flex;
						align-items: center;
						gap: 12px;
						line-height: 1;
					}
					.icon-box {
						min-width: 25px;
						min-height: 25px;
						display: flex;
						align-items: center;
						justify-content: center;
					}
					span {
						display: flex;
						align-items: center;
						justify-content: center;
						&::before {
							width: 7px;
							height: 15px;
							background: url("../img/icon/icon-menu/arr-top-level.svg");
							content: '';
						}

					}
					&.new {
						background: #28BE37;
						color: #fff;
					}
					&.hit {
						background: #F45D5D;
						color: #fff;
					}



				}

			}

			// 2 уровень
			& .grid-submenu > ul {

				a {
					font-family: "Rubik", sans-serif;
					font-size: 18px;
					font-weight: 700;
					min-height: 50px;
				}



				& > li {
					justify-content: flex-start;
					gap: 8px;
					span {
						display: flex;
						align-items: center;
						justify-content: center;
						&::before {
							width: 4px;
							height: 40px;
							background: url("../img/icon/icon-menu/arr-sub-level.svg") no-repeat center;
							content: '';
						}
						position: relative;
						&::after {
						    position: absolute;
						    top: 0;
							left: 0;
							width: 57px;
							height: 100%;
							content: '';
						}

					}

					&.main-cat {
						margin-bottom: 10px;
						a {
							font-family: "Nato Sans", sans-serif;
							font-size: 28px;
						}
						span {
							display: flex;
							align-items: center;
							justify-content: center;
							&::before {
								width: 7px;
								height: 15px;
								position: relative;
								top: 2px;
								background: url("../img/icon/icon-menu/arr-top-level.svg");
								content: '';
							}

						}
					}
					// 3 уровень
					& > ul {
						li {
							a {
								font-weight: 400;
								line-height: 40px;
								min-height: inherit;
							}
						}
					}

				}



			}






			.submenu {
				cursor: pointer;
				ul {
					opacity: 0;
					visibility: hidden;
					height: 0;

					&.show {
						opacity: 1;
						height: auto;
						visibility: visible;
						.cat-menu__nav-submenu span {
							outline: 1px solid red;
						}
					}
				}

			}



		}


	}
}
