}
.placeholder-container input:focus + .caption, .placeholder-container input:not(:placeholder-shown) + .caption, .placeholder-container textarea:focus + .caption, .placeholder-container textarea:not(:placeholder-shown) + .caption {
  opacity: 1;
  top: 0;
  padding: 0 15px;
  z-index: 3;
  font-size: 11px;
  transform: translate(0, -50%);
  color: #FFB444;
  background-color: #fff;
}
.placeholder-container textarea {
  height: 100%;
}
.placeholder-container .caption {
