  align-items: center;
  gap: 10px;
}
.checkbox span:before {
  transition: all 0.5s;
  border: 1px solid #CDCDCD;
  width: 25px;
  height: 25px;
  content: "";
}

.radio {
  position: relative;
  padding: 4px 0;
}
.radio__input {
  -webkit-appearance: none;
  appearance: none;
  position: absolute;
}
.radio__label {
