  z-index: 5;
  transform: rotateX(-90deg);
  transform-origin: 0 0;
  visibility: hidden;
  opacity: 0;
}
.select__body::after {
  position: absolute;
  top: 10px;
  right: 10px;
  min-width: 10px;
  height: 6px;
  background: url("../img/icon/arr/arr-select.svg");
