
// form
.placeholder-container {
	position: relative;




	input, textarea {
		color: inherit;
		width: 100%;
		height: 50px;
		position: relative;
		z-index: 2;
		padding: 20px 14px;
		border: 1px solid #CDCDCD;
		border-radius: 5px;


		&:focus {
			outline: 0;
		}
		&:focus + .caption, &:not(:placeholder-shown) + .caption{
			opacity: 1;
			top: 0;
			padding: 0 15px;
			z-index: 3;
			font-size: 11px;
			transform: translate(0, -50%);
			color: $accent2;
			background-color: #fff;
		}
	}

	textarea {
		height: 100%;
	}

	.caption {
		position: absolute;
		transition: all 1s;
		top: 50%;
		left: 20px;
		z-index: 2;
		transform: translate(0, -50%);
		pointer-events: none;
	}

	.error {
		border-bottom: 1px solid $accent !important;
		border-radius: inherit;
	}

	&__textarea {
		height: 200px;
	}

}


.checkbox {
	cursor: pointer;



	input {
		pointer-events: none;
		-webkit-appearance: none;
		appearance: none;
		position: absolute;


		&:checked + span {
			&:before {
				background: url("../img/icon/arr/check-black.svg") no-repeat center center;
			}
		}
	}

	span {
		pointer-events: none;
		display: flex;
		align-items: center;
		gap: 10px;
		&:before {
			transition: all .5s;
			border: 1px solid #CDCDCD;
			width: 25px;
			height: 25px;
			content: '';
		}

	}

}

.radio {
	position: relative;
	padding: 4px 0;

	// скрываем radio input по default
	&__input {
		-webkit-appearance: none;
		appearance: none;
		position: absolute;
	}

	// создаём свою радио кнопку
	&__label {
		display: flex;
		align-items: center;
		cursor: pointer;
		&::before {
			width: 23px;
			height: 23px;
			border: 1px solid #CFCFCF;
			content: '';
			margin-right: 10px;
			transition: all .3s;
			border-radius: 50%;
		}
	}



	// при checked
	&__input:checked + &__label:before {
		background-color: $accent;
	}


}

.btn {
	background-color: $accent2;
	font-size: 16px;
	padding: 7px 8px;
	cursor: pointer;
	transition: 1s;
	border: none;
	border-radius: 5px;
	text-align: center;
	line-height: 1;
	font-weight: 600;
	&:hover {
		opacity: .6;
	}
	&--red {
		background-color: #E43535;
	;
	}
	&--green {
		background-color: #339933;
	}
	&--silver {
		background-color: #D6D6D6;
	}

}

.select {
	position: relative;
	font-size: 12px;
	display: inline-flex;



	&.is-active {
		.select__body {
			transform: rotateX(0);
			visibility: visible;
			opacity: 1;
			transition: transform .3s linear;
		}
		.select__header {
			border: none !important;
		}

	}

	&__header {
		position: relative;
		cursor: pointer;
		display: inline-flex;
		align-items: center;
		padding: 0 10px 0 7px;
		border-radius: 8px;
		border: 1px solid #9A9A9A;
		min-width: 79px;
		height: 28px;
		gap: 10px;
		span {
			position: absolute;
			font-size: 11px;
			top: 0;
			left: 27px;
			transform: translateY(-60%);
			background-color: #fff;
		}
		&::after {
			min-width: 10px;
			height: 6px;
			background: url("../img/icon/arr/arr-select.svg");
			content: '';
		}
	}

	&__body {
		padding: 14px 0;
		background-color: #fff;
		border: 1px solid #cccccc;
		border-radius: 8px;
		left: 0;
		position: absolute;
		top: 0;
		width: 140px;
		z-index: 5;
		transform: rotateX(-90deg);
		transform-origin: 0 0;
		visibility: hidden;
		opacity: 0;

		&::after {
			position: absolute;
			top: 10px;
			right: 10px;
			min-width: 10px;
			height: 6px;
			background: url("../img/icon/arr/arr-select.svg");
			content: '';
			transform: rotate(-180deg);
			cursor: pointer;
		}

		input {
			-webkit-appearance: none;
			appearance: none;
			position: absolute;
		}
		&-item {
			display: flex;
			align-items: center;
			cursor: pointer;
			padding: 7px 14px;
			width: 100%;
			span {
				width: 130px;
			}
			&.active{
				&::after {
					min-width: 11px;
					height: 8px;
					background: url("../img/icon/arr/arr-select-green.svg");
					content: '';
				}

			}

			&:hover {
				background-color: #B4DFFB;
			}
		}
	}




}





// отключение scroll
.disable-scroll {
	position: relative;
	overflow: hidden;
	height: 100vh;
	width: 100%;
}



