// Настройки ===============================================================================================================================================================================================================================================================================================
@import "_base/settings";
@import "_base/components";
@import "_base/components/items";


.popup-cart {
	position: absolute;
	right: 0;
	background: #FFFFFF;
	border: 1px solid #DBDBDB;
	box-shadow: 0 0 35px 1px rgba(0, 0, 0, 0.15);
	border-radius: 5px 0 5px 5px;
	width: 250px;
	font-size: 13px;
	line-height: lh(13, 22);
	color: #333;
	z-index: 5;



	// корзина список
	&-list {
		 transform: rotateX(-90deg);
		 transform-origin: 0 0;
		 transition: transform .3s linear;
		 visibility: hidden;
		 opacity: 0;

		&__item {
			display: flex;
			align-items: center;
			gap: 14px;
			transition: all .5s;
			cursor: pointer;
			min-height: 40px;
			padding: 0 13px;



			.checkbox {
				font-size: 13px;
				font-weight: 400;



				input {
					pointer-events: none;
					-webkit-appearance: none;
					appearance: none;
					position: absolute;


					&:checked + span {
						&:before {
							background-image: url("../img/icon/arr/chec.svg");
							background-repeat: no-repeat;
							background-position: center center;
							background-color: var(--cart-color);
						}
					}
				}

				span {
					white-space: nowrap;
					transition: all 1s;
					gap: 8px;
					max-width: 167px;
					overflow: hidden;
					&::before {
						min-width: 29px;
						height: 16px;
						background-color: var(--cart-color);
						border: none;
						border-radius: 3px;
					}

				}

			}

			&-edit {
				min-width: 14px;
				min-height: 14px;
				max-width: 14px;
				max-height: 14px;
				background: url("../img/icon/pen.svg");
				margin-left: auto;
			}

			&-del {
				min-width: 16px;
				min-height: 16px;
				max-width: 16px;
				max-height: 16px;
				background: url("../img/icon/del.svg");

			}

			&-create {
				display: flex;
				align-items: center;
				gap: 18px;
				&::before {
					width: 21px;
					height: 21px;
					background: url("../img/icon/plus.svg");
					content: '';
				}

			}

			&:hover {
				background-color: #F1F1F1;
			}

		}
	}








}

.header {
	&--fixed {
		position: fixed;
		top: 0;
		z-index: 50;
		background-color: #fff;
		width: 100%;
		.header__row1 {
			display: none;
		}
	}

	&__row1 {
		background: #484848;
		color: #fff;
		font-size: 13px;
		letter-spacing: .3px;

		.container {
			display: flex;
			align-items: center;
			min-height: 31px;
			justify-content: space-between;
			ul {
				display: flex;
				gap: 20px;
			}
		}
	}

	&__row2 {
		border-bottom: 1px solid #DFDFDF;

		.container {
			display: flex;
			align-items: center;
			min-height: 84px;
			position: relative;
		}

		.btn {
			margin-left: 22px;
			min-height: 50px;
			gap: 13px;
			display: flex;
			align-items: center;
			padding: 0 25px;
			font-weight: 400;
			background-color: #FF9900;
		}

		&-search {
			height: 50px;
			margin-left: 18px;
			display: flex;
			flex-grow: 1;
			.placeholder-container {
				width: 100%;
				color: #CCC;
				position: relative;
				.btn {
					position: absolute;
					top: 5px;
					right: 5px;
					width: 40px;
					min-height: 40px;
					max-height: 40px;
					z-index: 2;
					border-radius: 3px;
					display: flex;
					align-items: center;
					justify-content: center;
					&::before {
						display: block;
						min-width: 19px;
						min-height: 19px;
						background: url("../img/icon/search.svg");
						content: '';

					}

				}
			}
		}

		&-right {
			min-height: 40px;
			margin-left: 48px;
			display: flex;
			gap: 23px;
			font-size: 13px;
			font-weight: 400;
			color: #494F54;


			&-price {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				&-caption {
					background: #B4DFFB;
					border-radius: 3px;
					font-size: 11px;
					font-weight: 700;
					line-height: lh(11, 22);
					text-align: center;
					position: relative;
					span {
						display: flex;
						align-items: center;
						justify-content: center;
						position: absolute;
						top: 0;
						right: 0;
						max-height: 20px;
						transform: translate(50%, -50%);
						border: 2px solid #FFFFFF;
						font-size: 10px;
						padding: 0 5px;
						color: #fff;
						line-height: 22px;
						font-weight: bold;
						border-radius: 100%;
						background-color: #FB1A1A;
					}
				}
				p {
					line-height: 1;
				}

			}

			&-favourites {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				align-items: center;

				&-icon {
					position: relative;
					&::before {
						display: block;
						width: 21px;
						height: 18px;
						background: url("../img/icon/heart.svg");
						content: '';
					}
					span {
						display: flex;
						align-items: center;
						justify-content: center;
						position: absolute;
						top: 0;
						right: 0;
						max-height: 20px;
						transform: translate(50%, -50%);
						background: #FB1A1A;
						border: 2px solid #FFFFFF;
						font-size: 10px;
						padding: 0 5px;
						color: #fff;
						line-height: 22px;
						font-weight: bold;
						border-radius: 100%;
					}
				}
				p {
					line-height: 1;
				}
			}

			&-user {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				align-items: center;

				&-icon {
					position: relative;
					&::before {
						display: block;
						width: 19px;
						height: 21px;
						background: url("../img/icon/user.svg");
						content: '';
					}
				}
				p {
					line-height: 1;
				}
			}

			&-cart {
				position: relative;
				cursor: pointer;
				align-self: flex-start;
				margin-left: 36px;

				&:hover {

					&::after {
						transform: rotate(0);
					}

					& > .popup-cart-list {
						transform: rotateX(0);
						visibility: visible;
						opacity: 1;
						z-index: 1000;
					}
				}

				&-caption {
					padding: 0 9px;
					background: #3E4347;
					border-radius: 3px 3px 0 0;
					color: #fff;
					font-size: 11px;
					font-weight: 700;
					line-height: lh(12, 22);
					display: flex;
					align-items: center;
					gap: 19px;

					&::after {

						line-height: 1;
						position: relative;
						width: 11px;
						height: 7px;
						background: url("../img/icon/arr/arr.svg");
						content: '';
						transform: rotate(180deg);
						transition: .3s;
					}


				}


			}




		}

	}


}

.sub-header {
	align-items: center;
	padding-top: 15px;
	padding-bottom: 20px;
	display: flex;
	gap: 22px;

	.btn {
		height: 47px;
		padding: 0 26px 0 13px;
		gap: 10px;
		align-items: center;
		border-radius: 10px;
		display: inline-flex;
		font-weight: 500;
		font-size: 14px;
		&--spes {
			background-color: #FFCB00;
			font-style: normal;
			&::before {
				width: 23px;
				height: 23px;
				background: url("../img/icon/money.svg");
				content: '';
			}

		}
		&--sale {
			background-color: #42ADE2;
			color: #fff;
			&::before {
				width: 25px;
				height: 34px;
				background: url("../img/icon/lightning.svg");
				content: '';

			}
		}
		&--new {
			background-color: #C7E755;
			&::before {
				width: 23px;
				height: 25px;
				background: url("../img/icon/siren.svg");
				content: '';
			}
		}
		&--ball {
			background-color: #B4DFFB;
			position: relative;
			font-weight: 700;
			span {
				position: absolute;
				top: -8px;
				right: -24px;
				background: #FF5656;
				border-radius: 15px;
				color: #fff;
				font-size: 14px;
				line-height: lh(14, 22);
				padding: 0 10px;
				display: block;
			}
			&::before {
				width: 32px;
				height: 32px;
				background: url("../img/icon/ball.svg");
				content: '';
			}
		}
	}
}

.footer {
	padding: 35px 0 45px;
	background: #484848;
	font-weight: 400;
	font-size: 14px;
	line-height: lh(14, 30);
