.modal-popup {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	border-radius: 5px 0 5px 5px;
	background: rgba(0, 0, 0, 0.30);
	z-index: 2000;
	display: flex;
	align-items: center;
	justify-content: center;
	opacity: 0;
	visibility: hidden;
	&.show {
		opacity: 1;
		visibility: visible;
	}

	// создать новую корзину
	.create-basket {
		background-color: #fff;
		padding: 26px 70px 40px;
		text-align: center;

		h3 {
			margin-bottom: 30px;
		}

		&__checkbox-box {
			display: flex;
			justify-content: center;
			gap: 15px;
			margin-bottom: 15px;

			.checkbox {

				input {
					pointer-events: none;
					-webkit-appearance: none;
					appearance: none;
					position: absolute;


					&:checked + span {
						&:before {
							background-image: url("../img/icon/arr/check-large.svg");
							background-repeat: no-repeat;
							background-position: center center;
						}
					}
				}

				span {
					transition: all 1s;
					&::before {
						min-width: 30px;
						height: 30px;
						background-color: var(--cart-color);
						border: none;
						border-radius: 3px;
					}

				}

			}

		}

		.placeholder-container {
			margin-bottom: 15px;
			max-width: 320px;
			color: #979797;
		}

		&__btns {
			display: flex;
			justify-content: space-between;
			gap: 25px;
			.btn {
				font-size: 14px;
				display: flex;
				justify-content: center;
				align-items: center;
				margin: 0;
				width: 149px;
				height: 43px;
				min-height: 37px;
			}
		}


	}

	// корзина удалить
	.delete-basket {
		border-radius: 5px 0px 5px 5px;
		border: 1px solid #DBDBDB;
		background: #FFF;
		box-shadow: 0 0 35px 1px rgba(0, 0, 0, 0.15);
		max-width: 250px;
		padding: 22px 25px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;


		h3 {
			font-size: 14px;
			font-style: normal;
			font-weight: 500;
			margin-bottom: 22px;
			text-align: center;
		}

		.checkbox {
			font-size: 13px;
			font-weight: 400;
			max-width: 168px;
			overflow: hidden;
			flex-grow: 1;
			margin-bottom: 36px;



			input {
				pointer-events: none;
				-webkit-appearance: none;
				appearance: none;
				position: absolute;


				&:checked + span {
					&:before {
						background-image: url("../img/icon/arr/chec.svg");
						background-repeat: no-repeat;
						background-position: center center;
					}
				}
			}

			span {
				white-space: nowrap;
				transition: all 1s;
				&::before {
					min-width: 29px;
					height: 16px;
					background-color: var(--cart-color);
					border: none;
					border-radius: 3px;
				}

			}

		}

		&__btns {
			display: flex;
			justify-content: space-between;
			width: 100%;
			max-width: 168px;

			.btn {
				display: flex;
				justify-content: center;
				align-items: center;
				margin: 0;
				width: 67px;
				min-height: 37px;
				color: #fff;
				font-size: 14px;
				font-weight: 500;
			}
		}

	}


}
